    magdalenamuskatitle: "Psycholog i Doradca",
    magdalenamuskatdescription:
      "Jestem psycholok i trenerk mwic po polsku i angielsku, absolwentk Uniwersytetu SWPS w Sopocie, z certyfikatem z Terapii Skoncentrowanej na Rozwizaniach (poziom 1) oraz dowiadczeniem w prowadzeniu warsztatw rozwojowych. Obecnie poszerzam swoje umiejtnoci w zakresie psychoterapii systemowej.\n\nPoniewa osobicie dowiadczyam ycia w rnych krajach, rozumiem, jak trudne moe by stawianie czoa rnicom kulturowym, adaptacja do nowej sytuacji yciowej.\n\nZapraszam do kontaktu w razie pyta lub chci umwienia sesji.",
