    justynaDescription:
      "Jestem anglojzycznym, certyfikowanym psychologiem, absolwentk Uniwersytetu Gdaskiego i psychoterapeutk w trakcie czteroletniego programu psychoterapeutycznego w Krakowskim Centrum Psychodynamicznym (KCP), rekomendowanym przez Polskie Towarzystwo Psychologiczne.\n\nMoje dowiadczenie zawodowe obejmuje prac w poradniach psychologiczno-pedagogicznych, instytucjach edukacyjnych oraz prywatnych praktykach psychologicznych i psychoterapeutycznych. Jestem zobowizana do cigego poszerzania swojej wiedzy poprzez studiowanie polskiej i midzynarodowej literatury z zakresu psychologii i psychoterapii, a take uczestnictwo w rnych programach szkoleniowych. Ukoczyam kursy interwencji kryzysowej, terapii traumy i lku oraz wywiadw motywacyjnych.\n\nPomagam dorosym i nastolatkom borykajcym si z wyzwaniami w relacjach interpersonalnych, lkiem, nisk samoocen, poczuciem zagubienia, zaburzeniami nastroju, traum, schorzeniami psychosomatycznymi i innymi. Wspieram take klientw w pokonywaniu wyzwa adaptacyjnych i trudnoci spoecznych, w tym zwizanych ze zmian miejsca zamieszkania. Mam dowiadczenie w pracy z dziemi i rodzinami z rnych rodowisk kulturowych, w tym z klientami z Ukrainy, Biaorusi, Armenii, USA i Woch.\n\nW mojej praktyce stosuj podejcie psychodynamiczne poczone z elementami CBT i innych metod terapeutycznych, dostosowujc moje podejcie do indywidualnych potrzeb kadego klienta.",

