    monikaDescription:
      "Jestem anglojzycznym psychologiem i psychoterapeut psychodynamicznym. Moje dowiadczenie zawodowe obejmuje prac w poradniach psychologiczno-pedagogicznych, instytucjach edukacyjnych i praktykach prywatnych.\n\nSpecjalizuj si w psychoterapii psychodynamicznej, interwencji kryzysowej, terapii traumy i lku oraz wywiadach motywacyjnych. Pracuj z dorosymi i nastolatkami stajcymi przed rnymi wyzwaniami, w tym problemami w relacjach interpersonalnych, lkiem, nisk samoocen, zaburzeniami nastroju, traum i trudnociami adaptacji kulturowej.\n\nMam dowiadczenie w pracy z klientami z rnych rodowisk kulturowych i rozumiem unikalne wyzwania, przed ktrymi stoj ekspatrianci i osoby adaptujce si do nowych rodowisk. Moje podejcie jest kulturowo wraliwe i dostosowane do indywidualnych potrzeb kadego klienta.\n\nZobowizuj si do tworzenia bezpiecznej przestrzeni wolnej od osdu, gdzie klienci mog eksplorowa swoje myli, uczucia i dowiadczenia. Moim celem jest wspieranie Ci w rozwijaniu wikszej samowiadomoci, odpornoci emocjonalnej i narzdzi potrzebnych do radzenia sobie z yciowymi wyzwaniami.",

