    magdalenaDescription:
      "Jestem polsko- i anglojzycznym psychologiem i trainerem. Ukoczyam Uniwersytet SWPS w Sopocie i zdobyam certyfikat I stopnia z Terapii Skoncentrowanej na Rozwizaniach, prowadzc warsztaty rozwojowe. Dodatkowo obecnie zdobywam dowiadczenie w psychoterapii systemowej.\n\nPoniewa osobicie dowiadczyam ycia w rnych krajach, rozumiem, jak trudne moe by stawianie czoa rnicom kulturowym i adaptacja do nowej sytuacji yciowej.\n\nJestem empatycznym i uwanym specjalist, wierzcym, e kady ma unikaln histori do opowiedzenia, a moj rol jest pomoc klientowi w spotkaniu z samym sob, aby mc uwaniej sucha wasnego dialogu, wasnej historii.",

